import axios from 'axios';
const db = app.firestore();

export const listTheories = async (teacherid)=>{
  try {
    let querySnapshot = await app.firestore().collection('repo/library/users').doc(teacherid).collection('theories').get();
    if (querySnapshot.docs) {
      const theories = querySnapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data()
        }
      })
      return theories
    }
  } catch (err) {
    console.log(err);
  }
}
