export const getTags = async()=>{
  const tagRef = await db.collection("resource").doc("tags").collection('tags').get();
  if (tagRef.docs) {
    const tags = tagRef.docs.map((doc) => {
      return {
        id: doc.id, ...doc.data()
      }
    });
    return tags
  }
}
export const createTheory = async(values,catalogId,sectionIndex )=>{
  try {

    console.log(values);
    console.log(catalogId);
    console.log(sectionIndex);
    console.log(typeof(values.data));
    const theory={name:values.name,tag:values.tag,detail:values.detail,created_by:values.created_by,author:values.author}

    theory.restart=0;
    theory.navigate=1;
    theory.created_time = new Date();
    theory.trial_maxquest=5;
    theory.trial_maxtime=30;
    theory.trial_maxentry=1;
    theory.random=1;
    theory.quest_feedback=0;
    theory.auto_next=0;
    const batch = db.batch();
    console.log(theory);
    const theoryRef = await db.collection('repo/library/users').doc(theory.created_by).collection('theories').doc();
    batch.set( theoryRef,theory);
    const sectionRef = await db.collection('repo/library/users').doc(theory.created_by).collection('catalogs').doc(catalogId).collection("sections").doc(sectionIndex);
      let data = await (await sectionRef.get()).data();
      if(data!=null)
      if("activities" in data){
        console.log(theoryRef);
        batch.update(db.collection('repo/library/users').doc(theory.created_by).collection('catalogs').doc(catalogId).collection("sections").doc(sectionIndex),{activities:firebase.firestore.FieldValue.arrayUnion({name:theory.name,type:"theory",ref:theoryRef})})
      } else {
        batch.update(db.collection('repo/library/users').doc(theory.created_by).collection('catalogs').doc(catalogId).collection("sections").doc(sectionIndex),{activities:[{name:theory.name,type:"theory",ref:theoryRef}]})
      }


    await batch.commit()
      console.log("theoryid:",theoryRef.id);
      theory.id = theoryRef.id;

    return true;
  }catch (err) {
    console.log(err);
  }
}
