import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listTheories } from '../../../../api/theory';
import useAuth from '../../../../hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const TheoryListView = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [theory, setTheories] = useState([]);
  const { user } = useAuth();
  const getTheories = useCallback(async () => {
    try {
      const theory = await listTheories(user.userId);
      
      if (isMountedRef.current) {
        setTheories(theory);
        console.log(theory);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getTheories();}, [getTheories]);
  return (
    <Page className={classes.root} title="Trường học">
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results theories={theory}/>
        </Box>
      </Container>
    </Page>
  );
};

export default TheoryListView;
