import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import FormatListNumberedIcon from '@material-ui/icons/FormatListNumbered';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên (tăng)'
  }
];

const applyFilters = (theories, query, filters) => {
  return theories.filter((theory) => {
    let matches = true;

    if (query) {
      const properties = ['title', 'description'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in theory) && theory[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && theory[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (theories, page, limit) => {
  return theories.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (theories, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = theories.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({ theories, ...rest }) => {

  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedtheories, setSelectedtheories] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedtheories([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAlltheories = (event) => {
    setSelectedtheories(event.target.checked
      ? theories.map((theory) => theory.id)
      : []);
  };

  const handleSelectOnetheory = (event, theoryId) => {
    if (!selectedtheories.includes(theoryId)) {
      setSelectedtheories((prevSelected) => [...prevSelected, theoryId]);
    } else {
      setSelectedtheories((prevSelected) => prevSelected.filter((id) => id !== theoryId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };


  const filteredtheories = applyFilters(theories, query, filters);
  const sortedtheories = applySort(filteredtheories, sort);
  const paginatedtheories = applyPagination(sortedtheories, page, limit);
  const enableBulkOperations = selectedtheories.length > 0;
  const selectedSometheories = selectedtheories.length > 0 && selectedtheories.length < theories.length;
  const selectedAlltheories = selectedtheories.length === theories.length;

  return (
    <Card className={clsx(classes.root)}{...rest}>
      <Tabs onChange={handleTabsChange} scrollButtons="auto" textColor="secondary" value={currentTab} variant="scrollable">
        {tabs.map((tab) => (
          <Tab key={tab.value} value={tab.value} label={tab.label}/>
        ))}
      </Tabs>
      <Divider />
      <Box p={2} minHeight={56} display="flex" alignItems="center">
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon fontSize="small" color="action">
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField label="Sắp xếp" name="sort" onChange={handleSortChange} select SelectProps={{ native: true }} value={sort} variant="outlined">
          {sortOptions.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox checked={selectedAlltheories} indeterminate={selectedSometheories} onChange={handleSelectAlltheories}/>
            <Button variant="outlined" className={classes.bulkAction} >
              Check
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox checked={selectedAlltheories} indeterminate={selectedSometheories} onChange={handleSelectAlltheories}/>
                </TableCell>
                <TableCell>
                  Tên
                </TableCell>
                <TableCell>
                  Mô tả
                </TableCell>
                <TableCell>
                  Catalog
                </TableCell>
                <TableCell align="right">
                  Action
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedtheories.map((theory) => {
                const istheorySelected = selectedtheories.includes(theory.id);
                return (
                  <TableRow hover key={theory.id} selected={istheorySelected}>
                    <TableCell padding="checkbox">
                      <Checkbox checked={istheorySelected} onChange={(event) => handleSelectOnetheory(event, theory.id)} value={istheorySelected}/>
                    </TableCell>
                    <TableCell>
                      <Box display="flex" alignItems="center">{theory.name}</Box>
                    </TableCell>
                    <TableCell>
                      {theory.desc}
                    </TableCell>
                    <TableCell>
                      {theory.catalog}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton component={RouterLink} to={"/teacher/management/theories/edit/"+ theory.id}>
                        <SvgIcon fontSize="small">
                        <EditIcon />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredtheories.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  theories: PropTypes.array.isRequired
};

Results.defaultProps = {
  theories: []
};

export default Results;
